.blog__heading-container {
  display: flex;

  align-items: center;
  justify-content: space-between;
}

.blog__all {
  color: $black;
}

.blog__list {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

.blog__item {
  max-width: 360px;
  margin: 0 10px;
  margin-bottom: 20px;
}

.blog__container {
  padding: 0 10px;
}

.blog__img {
  width: 100%;
  margin-bottom: 18px;
}

.blog__date {
  display: flex;

  margin-bottom: 15px;

  font-size: 14px;

  color: #434343;
}

.blog__heading {
  margin-bottom: 15px;

  font-size: 20px;
  font-weight: 600;
}

.blog__info {
  margin-bottom: 15px;

  font-size: 16px;

  color: #434343;
}

.blog__link {
  text-decoration: none;

  color: $yellow;
}
