.product__container {
  visibility: hidden;

  width: 100%;
  max-width: 140px;
  margin: 0 auto;
}

.product__list {
  width: 100%;
}

.product__item {
  display: flex;
  flex-direction: column;

  min-height: 377px;
  // transform: scale(0.7);

  flex-wrap: wrap;
  justify-content: flex-end;
}

.product__img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.product__img-container {
  display: flex;

  width: 53px;
  margin: 0 auto;

  flex-grow: 1;
  align-items: flex-end;
}

.swiper-slide-next {
  padding: 0 10px;

  // transform: scale(1);
  .product__container {
    visibility: visible;
  }
}

.product__price {
  margin-bottom: 13px;

  font-size: 20px;
  font-weight: 700;
}

.product__volume {
  font-size: 16px;

  color: #434343;
}

.product__button {
  background-color: $yellow;
}

@media(min-width: 500px) {
  .product__img-container {
    width: 81px;
  }

  .product__item {
    min-height: 514px;
  }
}

@media(min-width:$widthTablet) {
  .product__container {
    visibility: visible;

    max-width: 180px;
    padding: 10px;
  }

  .product__price {
    font-size: 30px;
  }
}
