.header {
  position: relative;

  min-height: 60px;
  padding-top: 15px;

  background: url('../img/header-big.png') 0 0 no-repeat;
  background-size: cover;
}

.header__container {
  display: flex;

  align-items: flex-start;
  justify-content: space-between;
}

.header__logo {
  position: relative;
  top: 10px;
}

.header__burger,
.header__search-button,
.header__form-close {
  border: none;
  background: transparent;

  fill: $white;
}

.header__lower {
  display: none;
}

.header__search {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;

  width: 100%;
  padding: 10px;

  background-color: #1f4c69f2;

  flex-wrap: wrap;
  justify-content: space-between;

  &--hidden {
    display: none;
  }
}

.header__input {
  width: 80%;
  padding: 10px;
}

.header__icon {
  display: none;
}

@media (min-width: $widthDesktop) {
  .header {
    max-height: 80px;
    padding-top: 20px;

    background-position: 0 -10px;
  }

  .header__burger {
    display: flex;

    width: 20%;

    justify-content: flex-start;
  }

  .header__icons {
    display: flex;

    width: 20%;

    justify-content: space-between;

    svg {
      fill: $white;
    }
  }

  .header__icon {
    position: relative;

    display: block;

    svg {
      fill: $white;
    }
  }

  .header__number {
    position: absolute;

    font-size: 12px;

    color: $white;
  }

  .header__svg {
    width: 358px;
    height: 99px;
  }

  .header__search {
    right: 0;
    left: auto;

    width: 40%;
  }

  .header__search-button svg {
    width: 30px;
    height: 30px;
  }

  .header__burger svg {
    width: 40px;
    height: 30px;
  }
}
