.footer {
  padding-bottom: 70px;

  background: url('../img/blue-bg.jpg') 0 0 no-repeat;
  background-size: cover;
}

.footer__list {
  position: fixed;
  z-index: 1000;
  bottom: 0;
  left: 0;

  display: flex;

  width: 100%;
  padding-top: 9px;
  padding-bottom: 9px;

  background-color: $white;

  justify-content: space-between;
}

.footer__item {
  position: relative;

  text-decoration: none;
}

.footer__number {
  position: absolute;
  right: -9px;
  bottom: - 1px;

  font-size: 12px;

  color: #ff0000;
}

.footer__logo {
  position: relative;
  bottom: 17px;

  display: flex;

  width: 100%;

  justify-content: center;
}

.footer__tel {
  display: block;

  width: fit-content;
  margin: 0 auto;
  margin-bottom: 10px;
  padding: 2px 27px;

  text-decoration: none;

  font-size: 20px;

  color: $white;
  border: 1px solid $white;
  border-radius: 20px;
}

.footer__address {
  margin-bottom: 20px;

  text-align: center;

  color: $white;

  span {
    display: block;

    width: 100%;
  }
}

.footer__socials {
  display: flex;

  max-width: 500px;
  margin: 0 auto;
  margin-bottom: 20px;

  align-items: center;
  justify-content: space-between;
}

.footer__social {
  transform: scale(1.3);
}

.footer__copyright {
  text-align: center;

  color: $white;
}

.footer__comma {
  display: none;
}

@media (min-width: $widthDesktop) {
  .footer__logo {
    top: -25px;
  }

  .footer__logo svg {
    width: 268px;
    height: 74px;
  }

  .footer__tel {
    font-size: 30px;
  }

  .footer__bottom {
    padding-bottom: 0;
  }

  .footer__comma {
    display: block;
  }

  .footer__list {
    display: none;
  }

  .footer__address {
    margin-bottom: 33px;

    font-size: 20px;

    span {
      display: inline;
    }
  }

  .footer__socials {
    margin-bottom: 40px;
  }
}
