.set__item {
  position: relative;

  margin: 0 5px;
  margin-bottom: 10px;

  &:nth-of-type(2),
  &:nth-of-type(3),
  &:nth-of-type(4) {
    .set__heading {
      top: 5px;
    }
  }

  &:nth-of-type(5) {
    .set__heading {
      right: 10px;
      bottom: 10px;
      left: auto;
    }
  }

  &:nth-of-type(7) {
    .set__heading {
      top: 10px;
      right: 10px;
      left: auto;
    }
  }
}

.set__heading {
  position: absolute;
  bottom: 5px;
  left: 10px;

  text-transform: uppercase;

  font-family: $agleecr;
  font-size: 24px;

  color: $white;
  text-shadow: 0 0 10px $black;
}

.set__list {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

@media(min-width: $widthDesktop) {
  .set__item {
    margin-bottom: 18px;

    img {
      width: 360px;
    }

    &:nth-of-type(1) img,
    &:nth-of-type(4) img {
      width: 755px;
      height: 240px;
    }

    &:nth-of-type(1),
    &:nth-of-type(2),
    &:nth-of-type(3),
    &:nth-of-type(3) {
      .set__heading {
        top: 10px;
        right: auto;
        bottom: auto;
        left: 10px;
      }
    }

    &:nth-of-type(6) {
      .set__heading {
        top: 10px;
        right: 10px;
        bottom: auto;
        left: auto;
      }
    }

    &:nth-of-type(7) {
      .set__heading {
        top: auto;
        right: 10px;
        bottom: 10px;
        left: auto;
      }
    }
  }
}
