.form__input {
  width: 100%;
  margin-bottom: 10px;
  padding: 6px 18px;

  border: solid 1px $grey;
  border-radius: 10px;

  &::placeholder {
    color: $grey;
  }
}

.form__button {
  margin: 0 auto;

  background-color: $yellow;
}

@media(min-width: $widthDesktop) {
  .form {
    display: flex;

    margin-bottom: 20px;

    justify-content: space-between;
  }

  .form__container {
    display: flex;
    flex-direction: column;

    width: 30%;
    min-width: 290px;

    justify-content: space-between;
  }

  .form__review-text {
    width: 59%;
  }

  .form__input {
    margin: 0;

    font-size: 20px;
  }

  .form__button {
    margin: 0;
    margin-left: auto;

    color: $white;
    background-color: $black;
  }
}
