.about-drink__heading {
  text-transform: uppercase;
}

.about-drink__info {
  margin-bottom: 15px;

  p {
    margin-bottom: 10px;
  }

  span {
    display: block;

    width: 100%;
  }
}

@media(min-width: $widthTablet) {
  .about-drink .container {
    display: flex;
  }

  .about-drink__info {
    margin-right: 36px;
  }
}

@media(min-width: $widthDesktop) {
  .about-drink__heading {
    margin-bottom: 5px;
  }

  .about-drink__info {
    font-size: 20px;

    p {
      margin-bottom: 20px;
    }
  }
}
