.main-slider {
  background-color: $yellow;
  background-image: url('../img/header-bubbles.png');
  background-repeat: no-repeat;
  background-position: 50% -125px;
}

.main-slider__slider {
  padding-top: 46px;
  padding-bottom: 21px;
}

.main-slider__img {
  margin-bottom: 54px;
}

.main-slider__heading {
  max-width: 225px;
  margin-bottom: 16px;

  font-size: 36px;
  line-height: 74%;
}

.main-slider__p {
  margin-bottom: 14px;

  font-size: 20px;
  font-weight: 700;

  color: $white;
}

.main-slider__button {
  margin: 0 auto;

  color: $white;
  background-color: $black;
}

.main-slider__slide {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

.main-slider__info {
  margin-left: 30px;
}

@media (min-width: $widthDesktop) {
  .main-slider {
    margin-bottom: 90px;
    padding-top: 40px;

    background-position: 0 -80px;

    picture {
      height: 407px;
    }
  }

  .main-slider__slider-container {
    display: flex;

    flex-wrap: wrap;
    align-items: flex-end;
  }

  .main-slider__slider {
    padding-bottom: 0;
  }

  .main-slider__img {
    display: block;

    width: 440px;
    height: 407px;
  }

  .main-slider__heading {
    max-width: 375px;
    margin-bottom: 26px;

    font-size: 62px;
  }

  .main-slider__p {
    margin-bottom: 37px;

    color: $black;
  }

  .main-slider__button {
    margin: 0;
    padding: 10px 18px;

    font-size: 25px;
  }

  .main-slider__slide {
    align-items: center;
  }
}

@media(min-width:1440px) {
  .main-slider {
    background-size: contain;
  }
}
