@media(max-width: $widthTablet) {
  .catalog {
    padding-top: 18px;

    .product__item {
      width: 48%;
      max-width: 150px;
      margin-right: 20px;
    }

    .product__container {
      visibility: visible;

      width: auto;
      max-width: 100%;
    }

    .product__img-and-price {
      display: flex;

      align-items: flex-end;
    }

    .product__price-circle {
      display: flex;

      width: 50%;

      justify-content: center;
      align-items: center;

      span {
        display: flex;

        width: 55px;
        height: 55px;
        margin-bottom: 10px;

        font-size: 20px;

        color: $white;
        border-radius: 50%;
        background-color: $black;

        align-items: center;
        justify-content: center;
      }
    }

    .product__img-container {
      width: 50%;
      max-width: 53px;
    }
  }
}

@media(min-width:$widthTablet) {
  .catalog {
    .product__item {
      width: 32%;
    }

    .product__price-circle {
      display: none;
    }

    .product__heading {
      font-size: 30px;
    }
  }
}

@media (min-width: 500px) {
  .catalog {
    .product__item {
      min-height: 100%;
    }
  }
}

.catalog__list {
  display: flex;

  margin-bottom: 75px;

  flex-wrap: wrap;
  justify-content: center;
}

.catalog__container {
  display: flex;

  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.catalog__filter {
  width: 50%;
  padding: 5px;

  font-size: 16px;

  color: $white;
  border: none;
  background-color: $grey;

  svg {
    display: none;

    margin-left: 5px;
  }

  &--active {
    color: $black;
    background-color: $yellow;
  }
}

.catalog__filters {
  display: flex;

  width: 100%;
}

.catalog__button {
  margin: 0 auto;
  padding: 10px 40px;

  font-weight: 400;

  border: 1px solid $black;
  border-radius: 10px;

  img {
    margin-right: 6px;
  }
}

@media(min-width: $widthTablet) {
  .catalog__filter {
    display: flex;

    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    svg {
      display: block;

      fill: $white;
    }

    &--active {
      svg {
        fill: $black;
      }
    }
  }

  .catalog__filters {
    width: 290px;
  }
}
