.card {
  padding-top: 20px;
}

.card__img {
  margin: 0 auto;
}

.card__switch {
  display: flex;

  flex-wrap: wrap;
}

.card__heading {
  margin-bottom: 1px;

  text-transform: uppercase;

  font-size: 20px;
}

.card__volume {
  margin-bottom: 15px;

  font-size: 16px;

  color: #434343;
}

.card__buttons {
  display: flex;

  width: 100%;
  max-width: 490px;
}

.card__pcs {
  display: flex;

  width: 100%;
  margin-bottom: 13px;
  padding: 7px;

  font-weight: 600;

  color: $white;
  background-color: $grey;

  justify-content: space-between;
  flex-grow: 1;

  &--active {
    color: $black;
    background-color: $yellow;
  }
}

.card__pcs-button {
  border: none;
  background: transparent;
}

.card__price {
  margin-bottom: 16px;

  font-size: 36px;
}

.card__button {
  margin: 0 auto;
  margin-bottom: 110px;
  padding: 8px 23px;

  background-color: $yellow;
}

.card__info {
  margin-bottom: 24px;

  line-height: 120%;
}

.card__features-list {
  display: flex;

  flex-wrap: wrap;
}

.card__feature {
  margin-right: 7px;
  margin-bottom: 10px;
  padding: 7px 10px;

  background-color: $yellow;
}

@media(min-width: 500px) {
  .card__button {
    margin-bottom: 20px;
  }

  .card__buttons {
    margin-bottom: 20px;
  }

  .card__main-container {
    display: flex;

    align-items: flex-start;
  }

  .card__container {
    width: 60%;
    max-width: 705px;
  }

  .card__img-container {
    width: 39%;
  }

  .card__img {
    width: 133px;
    height: auto;

    flex-grow: 1;
  }

  .card__pcs {
    margin-bottom: 0;
    padding: 11px;
  }

  .card__switch {
    align-items: flex-start;
  }
}

.card__price-container {
  width: 100%;
}

@media(min-width: $widthDesktop) {
  .card {
    padding-top: 120px;
  }

  .card__sub-heading {
    margin-bottom: 5px;
  }

  .card__sub-sub-heading {
    margin-bottom: 15px;

    font-size: 24px;
  }

  .card__switch {
    margin-bottom: 10px;
  }

  .card__heading {
    margin-bottom: 10px;

    font-size: 40px;
    line-height: 70%;
  }

  .card__volume {
    margin-bottom: 10px;

    font-size: 20px;
  }

  .card__buttons {
    margin-right: 15px;

    order: 1;
  }

  .card__pcs {
    margin-bottom: 0;

    align-items: center;
  }

  .card__button {
    margin: 0;
    padding: 9px;

    order: 1;
  }

  .card__price {
    font-size: 50px;
    font-weight: 800;
  }
}
