.about__circle {
  display: flex;

  width: 124px;
  height: 124px;
  margin: 0 auto;
  margin-bottom: 10px;

  border-radius: 50%;
  background-color: #e5e5e5;

  align-items: center;
  flex-wrap: wrap;
  justify-content: center;

  svg {
    width: 60%;
    height: auto;
  }
}

.about__item {
  width: 100%;
  margin-bottom: 13px;

  &:nth-of-type(2n) .about__circle {
    background-color: $yellow;
  }
}

.about__sub-heading {
  text-align: center;

  font-size: 16px;
  font-weight: 400;

  color: #434343;
}

@media(min-width: 460px) {
  .about__list {
    display: flex;

    justify-content: space-around;
  }
}

@media(min-width:$widthTablet) {
  .about__circle {
    width: 200px;
    height: 200px;
  }
}

@media(min-width: $widthDesktop) {
  .about__sub-heading {
    max-width: 140px;
    margin: 0 auto;

    font-size: 30px;
  }
}
