*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;

  font-family: $calibri;
}

body {
  margin: 0;

  font-size: 16px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  margin-bottom: 5em;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0, 0, 0, 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;

  @include media-desktop {
    padding: 0 30px;
  }
}

.heading {
  margin-bottom: 18px;

  font-size: 24px;
  font-weight: 700;

  @include media-desktop {
    margin-bottom: 50px;

    font-size: 40px;
  }
}

.button {
  display: flex;

  width: fit-content;
  padding: 10px;

  cursor: pointer;
  transition: opacity 0.35s;
  text-decoration: none;

  font-size: 20px;
  font-weight: 700;
  line-height: 18px;

  color: $black;
  border: none;
  border-radius: 10px;

  align-items: center;

  svg {
    margin-right: 5px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}

@media(min-width: 605px) {
  .swiper-pagination.swiper-pagination-bullets {
    bottom: 10px !important;
  }
}

@media(min-width: $widthDesktop) {
  .swiper-pagination.swiper-pagination-bullets {
    right: 0 !important;
    bottom: 10px !important;
    left: auto !important;

    width: 50% !important;
    padding-left: 40px;

    text-align: start !important;
  }
}

@media(min-width:$widthTablet) {
  .bg {
    position: absolute;
    z-index: -1;
    top: 100px;
    left: 0;

    width: 100%;
    height: 395px;

    background-color: $yellow;
  }
}
